.headerLogo{
    position: fixed;
    top: 1rem;
    left: 50%;
    width: 8rem;
    -webkit-transform: translate(-50%, 0%);
    transform: translate(-50%, 0%);
    z-index: 10;
}

.headerLink_group{
    position: relative;
    margin: 5rem auto 2rem auto;
    padding: 0 0;
    width: max-content;
    background-color: azure;
    font-size: 1rem;
    display: flex;
    justify-content: center;
}

.headerLink{
    position: relative;
    margin: 0.2rem 1rem;
    padding: 0.1rem 0.1rem;
    color: rgb(255, 255, 255);
    text-decoration-line: none;
    background-color: rgba(153, 0, 0, 0.666);
}

.headerLink_a{
    color: rgb(255, 255, 255);
    text-decoration-line: none;
}