body {
    background-color: rgb(216, 216, 216);
}

button{
    display: block;
    border-radius: 5px;
    height: 2rem;
    margin: auto 1rem;
}

.contentBack {
    position: relative;
    width: 90%;
    padding: 1rem 1rem;
    margin: 2rem auto;
    background-color: rgb(241, 241, 241);
}


.popMenuBack{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background-color: rgba(128, 128, 128, 0.5);
}


.popMenu{
    position: relative;
    width: max-content;
    top: 10vh;
    margin: 0 auto;
    padding: 2rem 2rem;
    background-color: rgb(255, 255, 255);
}

.popMenu_text{
    width: max-content;
    position: relative;
    margin: 1rem auto;
}

.popMenu_button{
    position: relative;
    margin: 2rem auto;
}

.popMenu_close{
    position: absolute;
    margin: 1rem 1rem;
    top: 0;
    right: 0;
}


.inputBox_group {
    position: relative;
    display: flex;
    justify-content: left;
    flex-wrap: wrap;
}


.inputBox {
    position: relative;
    margin: 2rem 1rem;
    padding: 0.2rem 0.2rem;
    background-color: azure;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    border-bottom: solid 1px black;
    width: 20rem;
}

.inputBoxShort {
    position: relative;
    margin: 2rem 1rem;
    padding: 0.2rem 0.2rem;
    background-color: azure;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    border-bottom: solid 1px black;
    width: 16rem;
}

.inputBoxSCS{
    position: relative;
    margin: 2rem 1rem;
    padding: 0.2rem 0.2rem;
    background-color: azure;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    border-bottom: solid 1px black;
}


.inputBox_label {
    position: relative;
    margin: 0 0;
    padding: 0 0;
    background-color: rgba(0, 255, 255, 0.1);
    /*width: 7rem;*/
    flex-basis: 35%;
}

.inputBox_colon {
    position: relative;
    margin: 0 0;
    padding: 0 0;
    bottom: 0.2rem;
    background-color: rgba(212, 255, 0, 0.1);
    /*width: 1rem;*/
    flex-basis: 5%;
}

.inputBox_form {
    position: relative;
    border: none;
    display: block;
    background-color: rgba(0, 0, 0, 0);
    padding: 0 0;
    font-size: 1rem;
    /*width: 10rem;*/
    width: 0rem;
    /*これ無いとflex-basisの割合指定ができない*/
    flex-basis: 60%;
}

.inputBox_formShort {
    position: relative;
    border: none;
    display: block;
    background-color: rgba(0, 0, 0, 0);
    padding: 0 0;
    font-size: 1rem;
    /*width: 10rem;*/
    width: 0rem;
    /*これ無いとflex-basisの割合指定ができない*/
    flex-basis: 27.5%;
}

.inputBoxSCS_label {
    position: relative;
    margin: 0 0;
    padding: 0 0;
    background-color: rgba(0, 255, 255, 0.1);
    width: 9rem;
}

.inputBoxSCS_colon {
    position: relative;
    margin: 0 0;
    padding: 0 0;
    bottom: 0.2rem;
    background-color: rgba(212, 255, 0, 0.1);
    width: 1rem;
}

.inputBoxSCS_form {
    position: relative;
    border: none;
    display: block;
    background-color: rgba(0, 0, 0, 0);
    padding: 0 0;
    font-size: 1rem;
    width: 2rem;
}

.addSeparateButton{
    position: relative;
    display: block;
    background-color: rgba(0, 0, 0, 0);
    padding: 0 0;
    font-size: 1rem;
    height: 1.5rem;
    width: 1.5rem;
}

.addSeparateSettingButton{
    background-color: antiquewhite;
}


.inputSeparator {
    position: relative;
    margin: 0.5rem auto;
    width: 90%;
    border-bottom: solid 2px black;
    height: 0;
}


.chordBox_area {
    position: relative;
    padding: 1rem 2rem;
    margin: 2rem auto;
    background-color: rgb(196, 136, 253);
}

.chordBox_group {
    position: relative;
    padding: 0 0 0 3.385rem;
    margin: 1rem 0;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    background-color: azure;
}

.chordBox {
    position: relative;
    width: 8rem;
    height: 6rem;
    padding: 0 0;
    margin: 0.5rem 0;
    /*background-color: aquamarine;*/
}

.chordBox_head {
    position: absolute;
    width: 3.385rem;
    left: 0;
    height: 6rem;
    padding: 0 0;
    margin: 0.5rem 0;
    /*background-color: aquamarine;*/
}

.chordBox_img {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0 0;
    margin: 0 0;
}

.inputBox_chord_0{
    position: absolute;
    width: 2rem;
    top: 2.5rem;
    left: 1rem;
}

.inputBox_chord_1{
    position: absolute;
    width: 2rem;
    top: 2.5rem;
    left: 3rem;
}

.inputBox_chord_2{
    position: absolute;
    width: 2rem;
    top: 2.5rem;
    left: 5rem;
}


.feelingMenu_area {
    position: relative;
    margin: 1rem 1rem;
    padding: 1rem 1rem;
    width: 70vw;
    height: 40vh;
    background-color: aquamarine;
    overflow-y: scroll;
}

.feelingMenu_group1 {
    position: relative;
    display: inline-block;
}

.feelingMenu_group2 {
    position: relative;
    display: flex;
    margin: 1rem 1rem;
    padding: 0.2rem 0.2rem;
    width: max-content;
    background-color: azure;
    font-size: 1rem;
    justify-content: center;
}

.feelingMenu_checkbox {
    position: relative;
    margin: 0 0;
    padding: 0 0;
    background-color: rgb(0, 255, 255);
}

.feelingMenu_label {
    position: relative;
    margin: 0 0;
    padding: 0 0.2rem;
    background-color: rgb(0, 255, 255);
}